import { FC } from 'react';

import { ListItem, Link, Separator } from './Breadcrumb.styles';
import { BreadcrumbListItemProps } from './Breadcrumb.types';

const BreadcrumbListItem: FC<BreadcrumbListItemProps> = ({ name, path }) => (
  <ListItem>
    <Link href={path}>{name}</Link>
    <Separator aria-hidden="true">&gt;</Separator>
  </ListItem>
);

export default BreadcrumbListItem;
