import { FC } from 'react';

import { List } from './Breadcrumb.styles';
import { BreadcrumbListProps } from './Breadcrumb.types';
import BreadcrumbListItem from './BreadcrumbListItem';

const BreadcrumbList: FC<BreadcrumbListProps> = ({
  breadcrumbs,
  BreadcrumbListItemCurrentPage,
}) => (
  <List>
    {breadcrumbs.length > 0 &&
      breadcrumbs.map(({ name, path }, index) => (
        <BreadcrumbListItem key={index} name={name} path={path} />
      ))}
    {BreadcrumbListItemCurrentPage}
  </List>
);

export default BreadcrumbList;
