import { FC } from 'react';

import { Nav, ListItemCurrentPage } from './Breadcrumb.styles';
import { BreadcrumbProps } from './Breadcrumb.types';
import BreadcrumbList from './BreadcrumbList';

const Breadcrumb: FC<BreadcrumbProps> = ({ breadcrumbs = [], currentPage }) => {
  if (!currentPage && breadcrumbs?.length === 0) {
    return null;
  }

  const BreadcrumbListItemCurrentPage = (
    <ListItemCurrentPage>{currentPage}</ListItemCurrentPage>
  );

  return (
    <Nav aria-label="breadcrumb">
      <BreadcrumbList
        breadcrumbs={breadcrumbs}
        {...(currentPage && {
          BreadcrumbListItemCurrentPage,
        })}
      />
    </Nav>
  );
};

export default Breadcrumb;
